.conteudo{
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;
}


.info{
    width: 60%;
    margin-right: 90px;
    margin-top: 40px;
    color: #FFFFFF;
}

input{
    border: 1px solid #999999; 
    border-radius: 22px;
    min-width: 250px;
    width: 80%;
    margin-right: 12px;
    background: transparent;
    color: #FFFFFF;
    font-size: 18px;
    padding: 10px;
}

button{
    border: 1px solid #999999; 
    border-radius: 22px;
    margin-right: 12px;
    background: #2D333B;
    color: #FFFFFF;
    font-size: 18px;
    padding: 10px;
}

.perfil{
    display: flex;
    margin: 40px 0;
}

.profile{
width: 120px;
height: 120px;
border-radius: 60px;
border: 2px solid #E5E5E5;
margin-right: 32px;
}

.perfil h3{
    color: #FFFFFF;
    font-size: 24px;
    margin: 0;
}

.perfil span{
    display: block;
    color: #FFFFFF;
    font-size: 12px;
    margin: 0;
}

.perfil p{
    color: #FFFFFF;
    font-size: 12px;
    margin: 20px 0 0 0;
}

.repositorio{
    font-size: 32px;
    color: #FFFFFF;
    margin: 12px 0;
    text-align: center;
}